import React from 'react';
import TextBlock from "../components/BlockText/TextBlock";
import BlocKImage from '../components/blockImage/BlocKImage';
import ItemMass from '../image/googleApi.png'
import client from '../image/googleClient.png'
import jsonClient from '../image/clientJson.png'

const Layer0 = () => {
    return (
      <div className="noda_context">
        <h2 style={{ textAlign: "center" }}>Установка Layer-Zero</h2>
        <div style={{ position: "relative", marginTop: "60px" }}>
          <TextBlock
            title={"Шестой этап "}
            text={{
              1: `Загружаем этот файл на сервер. Например, на сервере файл сохранен в ~/google-credentials.json. Мы почти у цели!`,
              2: "Редактируем .env файл, прописываем в свойство APP_KEY путь к загруженному файлу. AUTH_CACHE должен указывать на (еще пока не существующий) файл google-auth-cache.json в той же папке что и APP_KEY.",
              3: `nano "$INSTALL_DIR/lets-earn-research-zk-distrib/.env"`,
              4: "Например, так:",
              5: `AUTH_CACHE="/home/ubuntu/google-auth-cache.json`,
              6: `APP_KEY="/home/ubuntu/google-credentials.json"`,
              7: `Все, скрипт полностью настроен.`,
            }}
            top={"275px"}
            num={"6"}
          />
          <BlocKImage
            image={[jsonClient]}
            jusCont={"center"}
            top={"155px"}
            num={"5.5"}
          />
          <TextBlock
            title={"Пятый этап У"}
            text={{
              1: `После создания клиента нужно загрузить его JSON`,
            }}
            top={"220px"}
            num={"5"}
          />
          <BlocKImage
            image={[client]}
            jusCont={"center"}
            top={"185px"}
            num={"4.5"}
          />
          <TextBlock
            title={"четвёртый этап "}
            text={{
              1: `На следующей странице создаем свой email в качестве тестового пользователя.`,
              2: `Переходим в раздел https://console.cloud.google.com/apis/credentials и выбираем Create Credentials, OAuth Client ID.`,
            }}
            top={"165px"}
            num={"4"}
          />
          <BlocKImage
            image={[ItemMass]}
            jusCont={"center"}
            top={"135px"}
            num={"3.5"}
          />

          <TextBlock
            title={"Третий - разобраться с Google API."}
            text={{
              1: `Для управления работой скрипта и для мониторинга мы используем Google.Sheets, поэтому необходимо, чтобы наш скрипт имел права доступа к вашим Google документам.`,
              2: "Переходим на страницу управления API: ",
              3: `https://console.cloud.google.com/apis/library/sheets.googleapis.com и выбираем Enable. `,
              4: ` На странице https://console.cloud.google.com/apis/credentials/consent/edit вводим данные о себе, `,
              5: ` на странице Scopes должно быть так (используйте кнопку Add or Remove Scopes)`,
              6: ``,
            }}
            top={"110px"}
            num={"3"}
          />
          <TextBlock
            title={
              "Второй этап: Распаковываем архив и устанавливаем зависимости"
            }
            text={{
              1: "sudo apt-get update && sudo apt install -y unzip",
              2: `cd "$INSTALL_DIR" `,
              3: "unzip lets-earn-research-zk-distrib-1.0.1.zip",
              4: "cd lets-earn-research-zk-distrib",
              5: "yarn ",
              6: `Отключаем графические компоненты, если скрипт будет работать на сервере:`,
              7: `ed -i -e 's/opn(.*/console.log("Now open", authorizeUrl)/' "$INSTALL_DIR/lets-earn-research-zk-distrib/node_modules/@google-cloud/local-auth/build/src/index.js"`,
            }}
            top={"55px"}
            num={"2"}
          />
          <TextBlock
            title={"Первый этап Устанавливаем node.js v18."}
            text={{
              1: `INSTALL_DIR="$HOME"`,
              2: "wget https://nodejs.org/dist/v18.16.0/node-v18.16.0-linux-x64.tar.xz",
              3: "tar -xf node-v18.16.0-linux-x64.tar.xz",
              4: " cd node-v18.16.0-linux-x64/bin",
              5: `export PATH="$(pwd):$PATH" `,
              6: `npm install -g yarn`,
            }}
            top={"0px"}
            num={"1"}
          />
          <h1 style={{ textAlign: "center" }}>всё</h1>
        </div>
      </div>
    );
};

export default Layer0;